import React from 'react';
import {
  Container,
  TextField,
  Button,
  Typography,
  Box,
  Paper,
} from '@mui/material';
import { useCreateBlogPostMutation } from '@/services';

const AddPost = () => {

  const [createBlogPost, { isLoading }] = useCreateBlogPostMutation();

  const handleCreate = async () => {
    const newPost = {
      title: 'My New Blog Post',
      author: 'Jane Doe',
      excerpt: 'This is an example blog post created via RTK Query.',
      date: Date.now(),
    };

    try {
      const result = await createBlogPost(newPost);
      console.log('Created:', result);
    } catch (err) {
      console.error('Failed to create post:', err);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    handleCreate();
  };

  return (
    <Container maxWidth="sm" sx={{ mt: 4 }}>
      <Paper elevation={3} sx={{ p: 4 }}>
        <Typography variant="h5" align="center" gutterBottom>
          Add New Blog Post
        </Typography>

        <form onSubmit={handleSubmit}>
          <TextField label="Title" fullWidth margin="normal" />
          <TextField label="Author" fullWidth margin="normal" />
          <TextField label="Body" fullWidth multiline rows={6} margin="normal" />
          <Box textAlign="center" mt={3}>
            <Button type="submit" variant="contained" color="primary" disabled={isLoading}>
              {isLoading ? 'Submitting...' : 'Submit'}
            </Button>
          </Box>
        </form>
      </Paper>
    </Container>
  );
};

export default AddPost;
